* {
    background-color: black;
}
.game-area {
    position: relative;
    margin: 50px auto;
    width: 600px;
    height: 600px;
    border: 2px solid rgb(0, 252, 13);
}
  
.snake-dot {
    position: absolute;
    width: 2%;
    height: 2%;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgb(255, 0, 0);
    z-index: 2;
}
  
.snake-food {
    position: absolute;
    width: 2%;
    height: 2%;
    background: red;
    border: 1px solid #fff;
    z-index: 1;
}